.GameRenderer {
  width: 100%;
  height: 100%;
  background: #777;
  position: relative;
  overflow: hidden;
}
.game-object-renderer {
  position: absolute;
  bottom: 0;
  left: 0; }
  .game-object-renderer .sprite {
    position: absolute;
    bottom: 0; }
  .game-object-renderer .game-object-shadow {
    width: 45px;
    height: 25px;
    position: absolute;
    bottom: 0;
    left: 10px;
    background-image: url("/assets/images/gameObjects/selina_player.png");
    background-position: -640px -545px;
    background-size: 700px; }

.level-renderer {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%; }
  .level-renderer .level-background {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: auto;
    display: block; }
  .level-renderer .game-objects {
    position: absolute; }

.hud {
  position: absolute;
  top: 0;
  width: 100%;
  height: 139px;
  z-index: 10000;
  background: black;
  display: flex;
  align-items: center; }
  .hud .logo {
    margin-right: 0;
    margin-left: auto;
    height: 70%; }

.player-hud {
  color: #fff;
  width: 210px;
  height: 65px;
  text-transform: uppercase;
  margin-right: 3px;
  position: relative;
  font-size: 15px;
  flex-shrink: 0;
  background-image: url("/assets/images/ui/player_hud.png");
  background-size: contain;
  background-repeat: no-repeat; }
  .player-hud:first-of-type {
    margin-left: 3px; }
  .player-hud .name {
    position: absolute;
    background: #000;
    top: 2px;
    left: 20px;
    padding: 3px; }
  .player-hud .kills {
    position: absolute;
    top: 20px;
    right: 95px; }
  .player-hud .courage-bar {
    position: absolute;
    top: 34px;
    left: 95px; }
    .player-hud .courage-bar .courage {
      width: 6px;
      height: 12px;
      margin-right: 2px;
      display: inline-block;
      background-image: url("/assets/images/ui/hud_courage.png");
      background-size: 100%; }

@-webkit-keyframes selected-button-flash {
  0% {
    opacity: 1; }
  50% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes selected-button-flash {
  0% {
    opacity: 1; }
  50% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.TitleScreen {
  width: 100%;
  height: 100%;
  padding-top: 100px; }
  .TitleScreen .title-image {
    height: 50%;
    width: auto;
    display: block;
    margin: 0 auto 40px; }
  .TitleScreen .start-button-container {
    width: 250px;
    margin: 0 auto; }
    .TitleScreen .start-button-container a {
      text-decoration: none; }
    .TitleScreen .start-button-container .start-button {
      cursor: pointer;
      color: #fff;
      text-transform: uppercase;
      position: relative;
      padding: 12px 0;
      font-size: 18px;
      margin-left: 40px;
      font-weight: bold; }
      .TitleScreen .start-button-container .start-button .indicator {
        display: none;
        background: url("/assets/images/ui/hover_indicator.png");
        position: absolute;
        left: -53px;
        top: 6px;
        height: 30px;
        width: 40px;
        background-position: center center;
        background-size: 42px; }
      .TitleScreen .start-button-container .start-button.selected span {
        -webkit-animation: selected-button-flash 0.3s step-start infinite;
                animation: selected-button-flash 0.3s step-start infinite; }
      .TitleScreen .start-button-container .start-button:hover .indicator,
      .TitleScreen .start-button-container .start-button.selected .indicator {
