.instructions {
  color: #fff;
  text-align: center;
  margin-top: 50px;
  cursor: default; }
  .instructions .instructions-text {
    display: none;
    font-size: 11px;
    line-height: 1.5em; }
  .instructions:hover .hover-hint {
    display: none; }
  .instructions:hover .instructions-text {
    display: block; }

.PlayerSelectScreen {
  width: 100%;
  height: 100%;
  color: #fff;
  text-align: center;
  padding-top: 50px;
  font-weight: bold; }
  .PlayerSelectScreen .instructions {
    font-size: 20px;
