    margin-bottom: 10px; }
  .PlayerSelectScreen .player-types {
    margin: 0 auto; }
  .PlayerSelectScreen .player-type {
    cursor: pointer;
    width: 200px;
    height: 190px;
    background: url("/assets/images/ui/select_player_bg.png");
    background-size: 100%;
    background-repeat: no-repeat;
    margin: 20px;
    position: relative;
    padding: 24px 26px;
    display: inline-block; }
    .PlayerSelectScreen .player-type .player-image {
      width: 100%;
      height: auto;
      -webkit-filter: grayscale(100%);
              filter: grayscale(100%); }
    .PlayerSelectScreen .player-type .indicator {
      position: absolute;
      top: -15px;
      left: 0px;
      width: 80px;
      height: auto;
      display: none;
      -webkit-animation: blink 0.6s step-end infinite;
              animation: blink 0.6s step-end infinite; }
    .PlayerSelectScreen .player-type:hover .player-image {
      -webkit-filter: none;
              filter: none; }
    .PlayerSelectScreen .player-type:hover .indicator {
      display: block; }
    .PlayerSelectScreen .player-type.selected .player-image {
      -webkit-animation: blink 0.6s step-start infinite;
              animation: blink 0.6s step-start infinite;
      -webkit-filter: none;
              filter: none; }
    .PlayerSelectScreen .player-type.selected .indicator {
      display: block;
      -webkit-animation: none;
              animation: none; }
  .PlayerSelectScreen:not(.canSelect) .player-type {
    pointer-events: none; }


@-webkit-keyframes blink {
  0% { opacity: 1; }
  50% { opacity: 0; }
  100% { opacity: 1; }
}

@keyframes blink {
  0% { opacity: 1; }
  50% { opacity: 0; }
  100% { opacity: 1; }
}

